import { signOut } from "@/lib/auth";
import { LogOut } from "lucide-react";

export default function SignOutButton() {
  return (
    <div>
      <form
        action={async () => {
          "use server";
          await signOut({ redirectTo: "/" });
        }}
      >
        <button type="submit">
          <LogOut className="mr-2 h-4 w-4" />
          <span>Logout</span>
        </button>
      </form>
    </div>
  );
}
